@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  font-family: Orbitron, sans-serif;
  background: #474a56;
  color: #e4e5fc;
  /* colours: #00204a #005792 #00bbf0 #d9faff */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.user-menu-display{height:120px;display:flex;align-items:center}.user-menu-display:hover{background:#252525}.user-menu-display .user-image{cursor:pointer;height:90px;width:90px;border-radius:45px;margin-right:5%;margin-left:20px}.user-menu-display .user-info-container{display:flex;flex-direction:column;margin-right:10px}.user-menu-display .user-info-container .user-name{cursor:pointer;font-size:1em;margin-bottom:5px}.user-menu-display .user-info-container .user-id{cursor:pointer;font-size:.8em}
.menu-item{cursor:pointer;display:flex;flex-direction:row;align-items:center}.menu-item:hover{background:#252525;text-emphasis:none;-webkit-text-emphasis:none}.menu-item .menu-item-icon{height:40px;width:40px;margin:20px 20px}
.menu{background:#0b0b0d;width:100%;height:calc(100vh -20px);grid-row:span 2;border-right:2px solid #d3d5fd;z-index:3;padding-top:20px;display:flex;flex-direction:column}.menu .menu-items-container{height:85%;padding-top:50px}.menu .disclaimer-container{align-self:center;padding:20px}.menu .disclaimer-container .disclaimer{font-size:.8em}
.card{background:#0b0b0d;border-radius:20px;box-shadow:0px 1px 4px rgba(2,2,2,.1) inset,0px 0px 15px rgba(12,12,12,.9);display:flex;flex-direction:column;height:275px;align-items:center;margin:1%;padding:20px;transition:transform .2s ease-out}.card:hover{transform:scale(1.05);border:3px solid #bec0f8;padding:17px;cursor:pointer;box-shadow:0px 1px 3px rgba(0,0,0,.05) inset,0px 0px 10px rgba(82,168,236,.6)}.card .card-image{width:150px;height:150px;border-radius:75px;margin-bottom:10px}.card .card-field-container{display:flex;flex-direction:column;align-items:center}.card .card-field-container p{font-size:.75em;margin:5px 0}.card .card-field-container .card-id{font-size:1.4em}.card .card-field-container .card-field-one{font-size:1em}
.card-list-wrapper{height:calc(100vh - 70px);padding-top:10px;overflow-y:auto;-ms-overflow-style:none;scrollbar-width:none}.card-list-wrapper .card-list{display:flex;flex-flow:row wrap;justify-content:center}
.clear-button{display:flex;align-items:center;justify-content:center;cursor:pointer;height:20px;font-family:Orbitron,sans-serif;transition:transform .1s ease-in-out}.clear-button:active{transform:scale(0.9)}
.search-box-wrapper{display:flex;align-items:center;background:#2b2a2a;border-radius:12px}.search-box-wrapper .clear-button-wrapper{margin-right:10px}.search-box-wrapper .search-box{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace;width:100%;height:35px;background:#2b2a2a;border:none;border-radius:12px;color:#fff;text-align:center;font-size:.8em;text-indent:30px}.search-box-wrapper .search-icon{height:20px;margin-left:10px}
.go-back{cursor:pointer;height:45px;transition:transform .1s ease-in-out}.go-back:hover{transform:scale(1.08)}.go-back:active{transform:scale(1)}
.back-arrow{cursor:pointer;height:45px;transition:transform .1s ease-in-out}.back-arrow:hover{transform:scale(1.08)}.back-arrow:active{transform:scale(1)}
.forward-arrow{cursor:pointer;padding:0 5px;height:45px;rotate:180deg;transition:transform .1s ease-in-out}.forward-arrow:hover{transform:scale(1.08)}.forward-arrow:active{transform:scale(1)}

.menu-icon-wrapper{display:flex;justify-content:center;align-items:center;height:43px;width:43px;border-radius:24px;background:#fff}.menu-icon-wrapper .menu-icon{cursor:pointer;height:49px;transition:transform .1s ease-in-out}.menu-icon-wrapper .menu-icon:hover{transform:scale(1.08)}.menu-icon-wrapper .menu-icon:active{transform:scale(1)}
.line-break{height:0px;color:#fff;width:80%;padding:0px;margin:0 auto}.mini-card{cursor:pointer;display:flex;flex-direction:row;align-items:center;width:100%;padding:10px 0px;background:#0b0b0d}.mini-card:hover{background:#28282e}.mini-card .left-container{display:flex;margin-right:35px;margin-left:15px}.mini-card .left-container .index{margin-right:0px;margin-left:10px}.mini-card .left-container .image{width:100px;height:100px;border-radius:50px;margin-bottom:0px}.mini-card .info-container{display:flex;flex-direction:column;align-content:space-between}.mini-card .info-container h3{font-size:1.3em;margin:0;padding:1px}.mini-card .info-container h5{margin:0;padding:1px}.mini-card .info-container p{margin:0;padding:3px}.mini-card p{font-size:.75em}
.dropdown-list-wrapper .dropdown-list-back{background:#0b0b0d;border:2px solid #d3d5fd;border-radius:20px;box-shadow:0px 1px 4px rgba(2,2,2,.1) inset,0px 0px 15px rgba(12,12,12,.9);display:flex;flex-direction:column;height:80vh;width:350px;margin:5px 0 0 0;padding:0px;transition:transform .2s ease-out;overflow-y:auto;-ms-overflow-style:none;scrollbar-width:none}
.bar{display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:100%;height:60px;background:#0c0c0c;z-index:2}.bar .search-box-container{width:16%}.bar .left-container{width:40%}.bar .left-container .go-back-container{padding-left:40px}.bar .clear-button-container{padding-right:60px}.bar .right-container{display:flex;flex-direction:row;justify-content:flex-end;align-items:center;width:40%}.bar .right-container .dropdown-list-container .menu-icon-container{padding-right:40px;padding-bottom:2px}.bar .right-container .dropdown-list-container .dropdown-container{position:absolute;right:20px;top:0px;margin-top:60px;z-index:1}.bar .right-container .page-counter{font-size:.9em}.bar .right-container .arrows{text-align:right;padding:0 30px 0 15px}
.assetImage {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    width: 180px;
    height: 180px;
    margin: 20px;
    border-radius: 90px;
}

.header {
    display: grid;
    /* justify-content: center; */
    align-content: center;
    grid-template-columns: 1fr 10fr;
    background: #0b0b0d;
    border-radius: 20px;
    padding: 10px;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
}
.sub-info {
    background: #0b0b0d;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
    border-radius: 20px;
}
.details {
    display: grid;
    grid-template-rows: repeat(6, 1fr);
    grid-template-columns: 1fr 1fr;
    grid-column: span 2;
    align-items: center;
    background: #0b0b0d;
    padding: 10px;
    border-radius: 20px;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
    /* border-top: 1px solid #e4e5fc; */
}

.detailBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.error-container{width:100%;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.error-container .error-page{margin:20px 0}.error-container .error{font-size:5em}.error-container .not-exist{font-size:2em}.error-container .link-page{color:#888}
.asset-detailed-container{color:#e4e5fc;height:calc(100vh - 60px)}.asset-detailed-container .assetBox{position:relative;display:grid;grid-template-rows:1fr 3fr;grid-template-columns:minmax(450px, 1fr) minmax(600px, 2fr);grid-gap:20px;justify-content:space-between;padding:20px 30px 0 30px;height:95%}.asset-detailed-container .headerInfo{grid-column:2/3;margin-left:50px}
.container {
    height: 100vh;
    z-index: 0;
}

.asset-container-box {
    height: 100%;
}

.asset-homepage {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 5em;
}

.App{display:grid;grid-template-columns:minmax(300px, 1fr) 5fr}
